import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import Navbar from "./components/Navbar";
import HomePage from "./components/HomePage";
import AboutPage from "./components/AboutPage";
import ExperiencePage from "./components/ExperiencePage";
import ProjectsPage from "./components/ProjectsPage";
import ContactPage from "./components/ContactPage";
import MessageSentPage from "./components/MessageSentPage";

export default function App() {
  const [activePage, setActivePage] = useState('home');
  const [bgColor, setBgColor] = useState('bg-gray-50');

  const renderPage = () => {
    switch (activePage) {
      case 'about': setBgColor('bg-blue-50'); return <AboutPage key="about" />;
      case 'experience': setBgColor('bg-green-50'); return <ExperiencePage key="experience" />;
      case 'projects': setBgColor('bg-yellow-50'); return <ProjectsPage key="projects" />;
      case 'contact': setBgColor('bg-pink-50'); return <ContactPage key="contact" setActivePage={setActivePage} />;
      case 'messageSent': setBgColor('bg-purple-50'); return <MessageSentPage key="messageSent" setActivePage={setActivePage} />;
      default: setBgColor('bg-gray-50'); return <HomePage key="home" setActivePage={setActivePage} />;
    }
  };

  return (
    <motion.div className={`min-h-screen text-gray-900 overflow-hidden transition-colors duration-700 ${bgColor}`} initial={{ opacity: 0 }} animate={{ opacity: 1 }} transition={{ duration: 1 }}>
      <Navbar setActivePage={setActivePage} />
      <AnimatePresence mode="wait">
        <motion.div key={activePage} initial={{ x: 200, opacity: 0 }} animate={{ x: 0, opacity: 1 }} exit={{ x: -200, opacity: 0 }} transition={{ duration: 0.5 }}>
          {renderPage()}
        </motion.div>
      </AnimatePresence>
    </motion.div>
  );
}


