import { Button } from "./ui/button";
import { motion } from "framer-motion";

export default function HomePage({ setActivePage }) {
  const handleDownload = () => {
    const link = document.createElement('a');
    link.href = '/files/Kevin_CV.pdf';
    link.download = 'Kevin_CV.pdf';
    link.click();
  };

  return (
    <section className="flex flex-col md:flex-row items-center justify-center h-screen pt-24 px-6 md:px-10 gap-10">
      <div className="w-40 h-40 md:w-56 md:h-56 rounded-full bg-gray-300 overflow-hidden shadow-lg">
        <img src="/images/kevin-portrait.jpg" alt="Kevin" className="object-cover w-full h-full"/>
      </div>
      <motion.div initial={{ opacity: 0, x: 50 }} animate={{ opacity: 1, x: 0 }} transition={{ duration: 0.8 }} className="text-center md:text-left">
        <h2 className="text-3xl md:text-4xl font-bold mb-2">Hello, I'm Kevin!</h2>
        <p className="text-xl md:text-2xl text-gray-600 mb-6">Embedded Developer</p>
        <div className="flex justify-center md:justify-start space-x-4">
          <Button onClick={handleDownload}>Download CV</Button>
          <Button onClick={() => setActivePage('contact')} variant="outline">Contact Info</Button>
        </div>
      </motion.div>
    </section>
  );
}

