import { useState } from "react";
import emailjs from "emailjs-com";
import { Button } from "./ui/button";

export default function ContactPage({ setActivePage }) {
  const [formData, setFormData] = useState({ name: '', email: '', message: '' });

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    emailjs.send('service_mkvsaum', 'template_c5tyvkz', {
      from_name: formData.name,
      from_email: formData.email,
      message: formData.message,
      to_email: 'kevin.ronn@outlook.com'
    }, 'czzf9HulxDAliiWCj')
      .then(() => setActivePage('messageSent'))
      .catch((err) => {
        console.error('EmailJS error:', err);
        alert('Failed to send message, please try again later.');
      });
  };

  return (
    <div className="pt-32 px-6 md:px-10 text-center">
      <h2 className="text-3xl font-bold mb-4">Contact</h2>
      <p className="text-lg text-gray-700 mb-6">
        You can reach me via email, social media, or send me a message using the form below.
      </p>
      <form onSubmit={handleSubmit} className="max-w-xl mx-auto flex flex-col space-y-4">
        <input type="text" name="name" placeholder="Your Name" required className="border rounded px-4 py-2" value={formData.name} onChange={handleChange}/>
        <input type="email" name="email" placeholder="Your Email" required className="border rounded px-4 py-2" value={formData.email} onChange={handleChange}/>
        <textarea name="message" placeholder="Your Message" required rows={5} className="border rounded px-4 py-2" value={formData.message} onChange={handleChange}/>
        <Button type="submit">Send Message</Button>
      </form>
      <div className="flex flex-col items-center space-y-4 mt-6">
        <a href="mailto:kevin.ronn@outlook.com" className="text-blue-600 hover:underline text-lg">kevin.ronn@outlook.com</a>
        <div className="flex space-x-6 text-2xl">
          <a href="https://github.com/kevin" target="_blank" rel="noopener noreferrer" className="hover:text-gray-800"><i className="fa-brands fa-github"></i></a>
          <a href="https://linkedin.com/in/kevin" target="_blank" rel="noopener noreferrer" className="hover:text-blue-700"><i className="fa-brands fa-linkedin"></i></a>
          <a href="https://twitter.com/kevin" target="_blank" rel="noopener noreferrer" className="hover:text-sky-500"><i className="fa-brands fa-x-twitter"></i></a>
        </div>
      </div>
    </div>
  );
}

